import React from 'react'

import Header from './Header'
import SelectedShop from '../containers/SelectedShop'
import SelectedCoupon from '../containers/SelectedCoupon'
import CouponContent from './CouponContent'


class App extends React.Component {
	render() {
		return (
			<div>
				<Header />
				<SelectedShop />

				<SelectedCoupon />
				<CouponContent />
			</div>
		)
	}
}

export default App